import React from 'react';
import { Container, Grid, Card, CardMedia, CardContent, CardActions, Typography, Button, Box } from '@mui/material';

const projects = [
  {
    title: 'IOT Weather Reporting System Using Raspberry Pi',
    description: 'A system that reports weather conditions in real-time using IoT and a Raspberry Pi.',
    imageUrl: 'path/to/weather-project.jpg', // Replace with actual image path
    link: 'https://example.com/weather-project',
  },
  {
    title: 'Portfolio Website',
    description: 'A modern portfolio website built using React and Material-UI.',
    imageUrl: 'path/to/portfolio.jpg', // Replace with actual image path
    link: 'https://example.com/portfolio',
  },
  {
    title: 'Machine Learning Model for Data Analysis',
    description: 'An ML project focused on predictive data analysis using Python and Scikit-learn.',
    imageUrl: 'path/to/ml-project.jpg', // Replace with actual image path
    link: 'https://example.com/ml-project',
  },
];

const ProjectsSection = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 5, borderRadius: 2, mt: 4 }}>
      <Typography variant="h4" align="center" gutterBottom>
        Projects
      </Typography>
      <Grid container spacing={4}>
        {projects.map((project, index) => (
          <Grid item key={index} xs={12}>
            <Card
              sx={{
                display: 'flex',
                flexDirection: { xs: 'column', md: 'row' },
                alignItems: { md: 'center' },
                backgroundColor: '#f5f5f5',
                boxShadow: 3,
                '&:hover': {
                  boxShadow: 6,
                  transform: 'scale(1.02)',
                },
                transition: 'transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
              }}
            >
              <CardMedia
                component="img"
                image={project.imageUrl}
                alt={project.title}
                sx={{
                  height: { xs: 200, md: 150 },
                  width: { md: '30%' },
                  borderRadius: 1,
                  objectFit: 'cover',
                }}
              />
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-between',
                  flex: 1,
                  p: 2,
                }}
              >
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    {project.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {project.description}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button
                    size="small"
                    variant="contained"
                    color="primary"
                    href={project.link}
                    target="_blank"
                    sx={{ mt: 2 }}
                  >
                    View Project
                  </Button>
                </CardActions>
              </Box>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default ProjectsSection;
